import React, { Component } from 'react';
import {Polyline} from 'google-maps-react';

class Map extends Component {
	constructor(props) {
		super(props);

		const {lat, lng} = props.initialCenter;

		this.state = {
			currentLocation: {lat, lng}, 
			zoom: 17,
		};

		this.mymap = React.createRef();
		this.mymarker = React.createRef();
	}

	componentDidMount = () => {

	 // 	var data;
		// db.collection("coverage").get().then(querySnapshot => {
		//     querySnapshot.forEach(function(doc) {
		//     	if (doc.data().data) {
		//     		data = JSON.parse(doc.data().data);
		//     	} else {
		//     		data = [];
		//     	}

		//     });
		// }).then(() => {
		// 	this.setState({ 
		// 		context: canvas.getContext('2d'),
		// 		isDataFetched: true,
	 //  			data 
	 //  		}, () => console.log(this.state.data));
		// });


		this.setState({ // убрать при подключении Firestore
			data: [] //set test for testing purposes
		});


		if (this.props.centerAroundCurrentLocation) {
			if (navigator && navigator.geolocation) {
				navigator.geolocation.getCurrentPosition((pos) => {
					const coords = pos.coords;
					this.setState({
						currentLocation: {lat: coords.latitude, lng: coords.longitude}
	                }, this.props.updateLocationData(pos)) //update wrapper data
				})
			}
		}

		this.loadMap();
	}

	componentDidUpdate(prevProps, prevState) {
		if (prevProps.google !== this.props.google) {
			this.loadMap();
		}
		if (prevState.currentLocation !== this.state.currentLocation) {
			this.recenterMap();
		}
	}

	loadMap = () => {
		if (this.props.google) {

			var maps = this.props.google.maps;

			let {zoom} = this.props;
			//const {lat, lng} = initialCenter;
			const {lat, lng} = this.state.currentLocation;

			const center = new maps.LatLng(lat, lng);
			const mapConfig = Object.assign({}, {
				center: center,
				zoom: zoom
			});

			this.map = new maps.Map(this.mymap.current, mapConfig);

			this.map.addListener('zoom_changed', (evt) => {
				this.updateZoom();
			});
		}
	}

	updateZoom = () => {
		console.log(this.map.getZoom());
		this.setState({
			zoom: this.map.getZoom()
		});
	}

	recenterMap = () => {
		const map = this.map;
		const curr = this.state.currentLocation;

		const google = this.props.google;
		const maps = google.maps;

		if (map) {
			let center = new maps.LatLng(curr.lat, curr.lng)
			map.panTo(center);
		}
	}

	updateCurrent = (location) => {
		this.props.updateLocationData(location);
	}

	renderChildren = () => {
		const {children} = this.props;

		if (!children) return;

		return React.Children.map(children, c => {
			return React.cloneElement(c, {
				map: this.map,
				google: this.props.google,
				mapCenter: this.state.currentLocation
			});
		})
	}

	render() {
		let google = this.props.google;
		let zoom = this.state.zoom  ;

	  	return (
	  		<div 
	    			ref={this.mymap} 
	    			style={{width: "400px", height: "300px", position: "relative"}} 
		          	>
		          	Loading...
		          	{this.renderChildren()}
		          	</div>


		          	);

	  }
	}

	export default Map;

Map.defaultProps = {
	zoom: 16,
  // Minsk center, by default
  initialCenter: {
  	lat: 53.893009,
  	lng: 27.567444
  },
  centerAroundCurrentLocation: false
}