class Geolocation extends Component {
	constructor(props) {
		super(props);

	    //const {lat, lng} = this.props.initialCenter;

	    this.state = {
	    	//isInitCoordsReceived: false, // загрузка текущего местоположения
	    	isDataFetched: false, // обработка загрузки данных
	    	isChartView: false, // показ значений акселерометра в графике,
	    	isRecording: false,
	    	data: [],
	    	currentLocation: {}, 
	    	previousLocation: {},
	    	acceleration: {},
	    	context: null,
	    	zoom: 16,
	    	angle: 0,
	    	move: 0
	    };

	    this.canvas = React.createRef();
	    this.mymap = React.createRef();
	    this.mymarker = React.createRef();

	}

	componentDidMount = () => {
		const canvas = this.canvas.current;
		this.setState({ context: canvas.getContext('2d') });

	 // 	var data;
		// db.collection("coverage").get().then(querySnapshot => {
		//     querySnapshot.forEach(function(doc) {
		//     	if (doc.data().data) {
		//     		data = JSON.parse(doc.data().data);
		//     	} else {
		//     		data = [];
		//     	}

		//     });
		// }).then(() => {
		// 	this.setState({ 
		// 		context: canvas.getContext('2d'),
		// 		isDataFetched: true,
	 //  			data 
	 //  		}, () => console.log(this.state.data));
		// });


		this.setState({ // убрать при подключении Firestore
			context: canvas.getContext('2d'),
			isDataFetched: true,
			data: [] //set test for testing purposes
		});
	}

	componentDidUpdate(prevProps, prevState) {
	   //
	}

	toggleRecording = () => {
		if (!this.state.startRecording) {
			this.startRecording();
		} else {
			this.stopRecording();
		}
		
		this.setState({isRecording: !this.state.isRecording});  
	}

	startRecording = () => {
		if ('geolocation' in navigator) { // обработка отсутствия
			navigatorId = navigator.geolocation.watchPosition(this.updateLocationData, this.err, NAVIGATOR_OPTIONS);
	    	if (window.DeviceMotionEvent) { //обработка отсутствия
	    		window.addEventListener("devicemotion", this.updateAccelerationData); 
	    	}
	    }	
	}

	err = () => {
		console.log("error");
	}

	updateAccelerationData = (ev) => {
		let x = ev.acceleration.x;
		let y = ev.acceleration.y;
		let z = ev.acceleration.z;

		if (x !== null && y !== null && z !== null) {
			this.setState({
				acceleration: { x, y, z}
			}, () => {
				if (line === this.canvas.current.width) line = 0;
				
				this.drawBackground("white", 0);

				var index = 1;
				let accel = this.state.acceleration;
				for (var prop in accel) {
					this.drawLine(accel[prop], index);
					index++;
				}
				line++;

				this.drawBackground("grey", 1);
				for (var i = 2; i < 8; i++) this.drawBackground("white", i);
			})
		}
	}

	toRad = num => {
		return num * Math.PI/180;
	}

	calculateDistance = (lat1, lng1, lat2, lng2) => {
		var dLat = this.toRad(lat2 - lat1);
		var dLng = this.toRad(lng2 - lng1);

		var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) * Math.sin(dLng / 2) * Math.sin(dLng / 2);

		var distance = EARTH_RADIUS * 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
		//console.log(distance); // в метрах
		return distance;
	}

	calculateAngle = (lat1, lng1, lat2, lng2) => {

		var dLng = (lng2 - lng1);

		var y = Math.sin(dLng) * Math.cos(lat2);
		var x = Math.cos(lat1) * Math.sin(lat2) - Math.sin(lat1) * Math.cos(lat2) * Math.cos(dLng);

		var brng = Math.atan2(y, x);

		var pi = Math.PI;
		brng = brng * (180/pi);
		brng = (brng + 360) % 360;
	    brng = 360 - brng; // count degrees counter-clockwise - remove to make clockwise

	    return brng;

	}

	drawLine = (value, index) => {
		let ctx = this.state.context;
		var axis = index * 100 - 50;

		ctx.beginPath();
		ctx.strokeStyle="black";
		ctx.moveTo(line, axis + value * 5);
		ctx.lineTo(line, axis);
		ctx.stroke();
	}

	drawBackground = (color, offset) => {
		let ctx = this.state.context;

		ctx.beginPath();
		ctx.strokeStyle = color;
		ctx.moveTo(line + offset, 0);
		ctx.lineTo(line + offset, 300);
		ctx.stroke();
	}

	updateLocationData = (location) => {
		var prev = this.state.currentLocation;

		var lat = location.coords.latitude;
		var lng = location.coords.longitude;
		var timestamp = location.timestamp;

		var distance = this.calculateDistance(prev.lat, prev.lng, lat, lng); // в метрах
		var angle = this.calculateAngle(prev.lat, prev.lng, lat, lng);
		var time = Math.abs(prev.timestamp - timestamp)/ 1000; // в секундах
		var speed = (distance/time) ? (distance/time) : 0;

		this.setState({	
			previousLocation: prev,
			currentLocation: { lat,	lng, speed,	timestamp },
			data: [...this.state.data, { lat, lng }],
			angle: (Math.abs(angle - this.state.angle) > 22) ? angle : this.state.angle,
			move: this.state.move + distance
		});
	}

	updateResultingData = () => {
		
	}

	stopRecording = () => {
		console.log("stopped");
		console.log(this.state.data);

		//db.collection("coverage").doc(topleft).set({data: JSON.stringify(this.state.data)});

		navigator.geolocation.clearWatch(navigatorId);
		window.removeEventListener("devicemotion", this.updateAccelerationData);
	}

	toggleChart = () => {
		this.setState({isChartView: !this.state.isChartView});
	}

	resetMove = () => {
		this.setState({move: 0})
	}

	render() {
		let google = this.props.google;
		let zoom = this.state.zoom;
		let recordingButtonText = this.state.isRecording ? "Stop Recording" : "Start Recording";
		let chartViewButtonText = this.state.isChartView ? "Hide Chart" : "Show Chart";

		if (this.props.loaded) {
			return (
				<div>
					<button onClick={this.toggleRecording} disabled={!this.state.isDataFetched}>{recordingButtonText}</button>
					<button onClick={this.toggleChart}>{chartViewButtonText}</button>
					<div>lat={this.state.currentLocation.lat}</div>
					<div>lng={this.state.currentLocation.lng}</div>
					<div>speed={this.state.currentLocation.speed}</div>
					<div>angle={this.state.angle}</div>
					<canvas id="canvas" width="400" height="300" ref={this.canvas} style={{display: this.state.isChartView ? "block" : "none"}}/>

					<div id="map" style={{width: "400px", height: "300px", position: "relative"}}>
					<Map 
						centerAroundCurrentLocation
						google={this.props.google}
						angle={this.state.angle}
						data={this.state.data}
						updateLocationData={this.updateLocationData}
						>

						<Marker 
							position={{lat: this.state.currentLocation.lat, lng: this.state.currentLocation.lng}} 
							angle={this.state.angle}
							zoom={this.state.zoom}
							move={this.state.move}
							resetMove={this.resetMove}
						/>
						<Polyline
							path={this.state.data}
							strokeColor="#0000FF"
							strokeOpacity={0.8}
							strokeWeight={4} 
						/>
					</Map>
					</div>
				</div>
				);
		} 
	}
}

//export default Geolocation;

export default GoogleApiWrapper({
	apiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY
})(Geolocation);

